<template>
<div>
    <div class="nav-bar">
      <navBar :color='"black"'/>
    </div>
    <div class="article">
        <h1 class="title">{{article.title}}</h1>
        <div class="container">
        </div>
    </div>
</div>
</template>

<script>
import navBar from '@/components/navigatorBar.vue'
import article from '@/network/article'
export default {
    components: {
        navBar
    },
    data () {
        return {
            article: {}
        }
    },
    created() {
        const id = this.$route.params.title
        this.getArticle(id)
    },
    methods: {
        async getArticle (id) {
            let res = await article.getArticle(id)
            this.article = res.data
            const container = document.querySelector('.container')
            container.innerHTML = res.data.content
        }
    }
}
</script>

<style lang="scss" scoped>
.container {
    margin-top: 60px;
}
.article{
    margin: 10px auto;
    max-width: 750px;
    text-align: left;
}
.nav-bar{
    border-bottom: 1px solid rgb(223, 242, 248);
}
</style>

